<template>
  <div class="pt" ref="pt">
    <div class="pt-content">
      <p class="text">至少选择6个红球1个篮球<span class="icon-shark"></span></p>
      <div class="cp-ballType border-1px">
        <div class="cp-box">
          <div class="ballItem" v-for="(item,index) in redBall">
            <span class="ball whiteBall1" @click.stop="selectRed(index,$event)"
                  :class="{redBall:item}">{{index+1}}</span>
          </div>
        </div>
      </div>
      <div class="cp-ballType">
        <div class="cp-box">
          <div class="ballItem" v-for="(item,index) in blueBall">
            <span class="ball whiteBall2" @click.stop="selectBlue(index,$event)" :class="
                  {blueBall:item}">{{index+1}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue';
  import BScroll from 'better-scroll';
  const REDCOUNT = 33;
  const BLUECOUNT = 16;
  export default{
    data() {
      return {
        redBall: [],
        blueBall: []
      };
    },
    created() {
      // 添加红球
      for (let i = 1; i <= REDCOUNT; i++) {
        this.redBall.push(false);
      }
      // 添加篮球
      for (let i = 1; i <= BLUECOUNT; i++) {
        this.blueBall.push(false);
      }
      this.$nextTick(() => {
        this._initScroll();
      });
    },
    mounted() {
      this.$nextTick(() => {
        this._initScroll();
      });
    },
    methods: {
      _initScroll() {
        if (!this.ptBallScroll) {
          this.ptBallScroll = new BScroll(this.$refs.pt, {
            click: true,
            bounce: false
          });
        } else {
          this.ptBallScroll.refresh();
        }
      },
      selectRed(num, event) {
        if (event) {
          if (!event._constructed) {
            return;
          }
        }
        if (this.redBall[num] === false) {
          Vue.set(this.redBall, num, true);
        } else {
          Vue.set(this.redBall, num, false);
        }
        this.$nextTick(() => {
          this.resultParent();
        });
      },
      selectBlue(num, event) {
        if (event) {
          if (!event._constructed) {
            return;
          }
        }
        if (this.blueBall[num] === false) {
          Vue.set(this.blueBall, num, true);
        } else {
          Vue.set(this.blueBall, num, false);
        }
        this.$nextTick(() => {
          this.resultParent();
        });
      },
      resultParent() {
        this.$emit('select', this.redNums, this.blueNums);
      },
      clearNums() {
        for (let i = 0; i < REDCOUNT; i++) {
          Vue.set(this.redBall, i, false);
        }
        for (let i = 0; i < BLUECOUNT; i++) {
          Vue.set(this.blueBall, i, false);
        }
      }
    },
    computed: {
      redNums() {
        let Nums = [];
        for (var i in this.redBall) {
          if (this.redBall[i]) {
            i = parseInt(i) + 1;
            if (i < 10) {
              i = '0' + i;
            }
            Nums.push(i.toString());
          }
        }
        return Nums;
      },
      blueNums() {
        let Nums = [];
        for (var i in this.blueBall) {
          if (this.blueBall[i]) {
            i = parseInt(i) + 1;
            if (i < 10) {
              i = '0' + i;
            }
            Nums.push(i.toString());
          }
        }
        return Nums;
      }
    }
  };
</script>

<style rel="stylesheet/stylus" lang="stylus">
  @import "../../../common/stylus/mixin.styl";
  .pt
    position:fixed
    z-index:50
    top:40px
    bottom:50px
    left:0
    right:0
    width:100%
    overflow:hidden
    background-color:#f8f8f0
    .pt-content
      .text
        position:relative
        margin:0
        padding:20px 10px 0px
        text-align:right;
        font-size:16px
        color:#938268;
        .icon-shark
          display:block
          position:absolute
          top:12px
          left:10px
          width:40px
          height:36px
          background-size:100% 100%
          background-repeat:no-repeat
          background-image:url('../images/shark.png');
      .cp-ballType
        padding:10px 0;
        width:100%
        border-1px(#cdcdcd)
        &:last-child:after
          display:none
        .cp-box
          display:flex
          flex-flow:wrap
          margin:0 auto
          width:280px
          .ballItem
            padding-top:5px
            width:35px
            text-align:center
            .ball
              display:block
              width:33px
              height:33px
              text-align:center
              font-size:13px
              font-weight:600
              line-height:33px
              cursor:pointer
              background-repeat:no-repeat
              background-size:100% 100%
            .whiteBall1
              background-image:url('../images/whiteB.png')
              color:#c33f51
            .whiteBall2
              background-image:url('../images/whiteB.png')
              color:#3373c1
            .redBall
              background-image:url('../images/redB.png')
              color:#fff
            .blueBall
              background-image:url('../images/blueB.png')
              color:#fff
</style>



