// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import VueRouter from 'vue-router';
import ssc from 'components/ssc/ssc';
import d3 from 'components/3d/3d';
import dlt from 'components/dlt/dlt';
import k3 from 'components/k3/k3';

import 'common/stylus/index.styl';

Vue.use(VueRouter);

Vue.config.productionTip = false;

const routes = [
  {path: '/ssc', component: ssc},
  {path: '/dlt', component: dlt},
  {path: '/d3', component: d3},
  {path: '/k3', component: k3}
];

const router = new VueRouter({
  routes,
  linkActiveClass: 'active'
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router
});



